import axios from '@/libs/api.request'
import config from '@/config'
//import Qs from 'qs'
 let apiPath= '/clerp-app-api'  //

/**
* @description   /common/sp_PhoneInfoCONew/findList

	DateFr()
	DateTo ()
	Spec (  *)
	Qty (, )
	PO (PO, )
	NoDeli (0:1:,2:)
	SizeL ()
	SizeW ()
	ArtID ()

	(up_CustID)
	(ct_Desc)
	(ui_UPNo)
	(ui_UPName)
	(ui_Price)
	(up_StartDate)
	(Csize)
*/
export const getProgress = ({ DateFr,DateTo,Spec,Qty,PO,NoDeli,SizeL,SizeW,ArtID,size,current }) => {
  //
  let data = {
    DateFr,DateTo,Qty,PO,NoDeli,SizeL,SizeW,ArtID,
	size,current
   }
   
 return axios.request({
    url: `${apiPath}/common/sp_PhoneInfoCONew/findList`,
    data,
    method: 'POST',
  })
}

/**
* @description   BY ID
* 
*   coNo()
  
	(List)
*/
export const getProgressDetail = ({ coNo,size,current }) => {
  //
  let data = {
    coNo,size,current
   }
   
 return axios.request({
    url: `${apiPath}/common/detailSysOrder/get`,
    data,
    method: 'POST',
  })
}




