import config from '@/config'
const baseUrl = config.isRunApp ? config.baseUrl.pro : config.baseUrl.dev
let currentToken=''
import Request from './request/request'

const axios = new Request()

axios.setConfig((config) => { /*  */
	config.baseUrl = baseUrl /*  */
	config.header = {
		'Content-Type': 'application/x-www-form-urlencoded',
		//'Content-Type': 'application/json;charset=UTF-8',
		token:'', //uni.getStorageSync("TOKEN") 
		time: Date.now().toString()
	}
	return config
})

axios.interceptor.request((config, cancel) => { /*  */
	config.header = {
		...config.header,
	}
	//debugger
	//   Content-Type
	if(config['Content-Type']!=null){
		config.header['Content-Type'] = config['Content-Type']
	}
	currentToken =uni.getStorageSync("TOKEN")==''? '':JSON.parse(uni.getStorageSync("TOKEN")) || ''
	console.warn('==Token==='+currentToken)
	config.header.token=currentToken

	uni.showLoading({
		mask: true,
		title: '...'
	})
	/*
  if (!token) { // tokencancel catch() 
    cancel('token ') // catch((err) => {}) err.errMsg === 'token '
  }
  */
	return config
})

axios.interceptor.response((res) => { /*  */
     uni.hideLoading()
   if(res.status===10000 || res.data.status===10000){
	   uni.showToast({
	       title: '',
	       duration: 2000
	   });
	   try {
	       uni.removeStorageSync('TOKEN');
	   } catch (e) {
	       // error
	   }
	  
   }else{
	  return res 
   }
   
	
}, (error) => { // 
   // debugger
	uni.hideLoading()
	//return Promise.reject(error)
	 if (error && error.statusCode) {
	        switch (error.statusCode) {
	          case 400:
	            error.errMsg = ''
	            break
	          case 401:
	            error.errMsg = ''
	            break
	          case 403:
	            error.errMsg = ''
	            break
	          case 404:
	            error.errMsg = ','
	            break
	          case 405:
	            error.errMsg = ''
	            break
	          case 408:
	            error.errMsg = ''
	            break
	          case 500:
	            error.errMsg = ''
	            break
	          case 501:
	            error.errMsg = ''
	            break
	          case 502:
	            error.errMsg = ''
	            break
	          case 503:
	            error.errMsg = ''
	            break
	          case 504:
	            error.errMsg = ''
	            break
	          case 505:
	            error.errMsg = 'http'
	            break
	          default:
	            error.errMsg = `${error.response.status}`
	        }
	      } else {
	        error.errMsg = ''
	      }
	      return Promise.reject(error.errMsg + ' ' + error.statusCode)
})

export default axios