import axios from '@/libs/api.request'
import config from '@/config'
//import Qs from 'qs'
 let apiPath= '/clerp-app-api'  //

/**
* @description  
* @params {  DateFr,DateTo,Spec,Qty,PO,NoDeli,SizeL,SizeW,ArtID,size,current }
*/
export const getPaperBoxProgress = ({  DateFr,DateTo,Spec,Qty,PO,NoDeli,SizeL,SizeW,ArtID,size,current }) => {
  //
  let data = {
     DateFr,DateTo,Spec,Qty,PO,NoDeli,SizeL,SizeW,ArtID,size,current
   }
   
 return axios.request({
    url: `${apiPath}/paperBox/progress`,
    data,
    method: 'POST',
  })
}

