/// 
export const updateStorehouseArrange = ({map}) => {
  //debugger
  //
  let data = map
 return axios.request({
   'Content-Type': 'application/json;charset=UTF-8',
    url: `${apiPath}/scan/updateStorehouse`,
    data,
    method: 'POST',
  })
}
